@media (min-width: 992px)
{.modal-lg {
    max-width: 538px !important;
}}

@media (min-width: 576px)
{.modal-dialog {
    min-width: 30%;
    margin: 0 !important;
}}

.modal{
    margin: 0px;
    display: flex !important;
    justify-content: center;
    align-items: center;
}

table{
    width: 100%;
    tr{
        height: 45px;
    }
    th, td{
        padding-left: 1rem;
        padding-right: 1rem;
    }
    tbody{
        overflow: auto;
    }
}

.captcha-input{
    border: none;
    font-size: 30px;
    width: 40px;
    text-align: center;
    border-left: none;
    border-right: none;
    border-bottom: 2px solid #DCDCDC;
    margin: 10px;
}
.captcha-input:focus{
    outline-width: 0;
}
.captcha-box{
    width: 190px; 
    background-color: white;
    padding: 10px;
    border: 2px solid #DCDCDC;
    box-sizing: border-box;
    border-radius: 4px;
}

.disabled{
    cursor: none;
    pointer-events: none;
}